body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.inwrap {
  margin: 5px;
  width: 200px;
}

.ant-select {
  width: 100%;
}

.ant-btn {
  margin: 1rem;
}

.ant-input-search-enter-button + .ant-input-group-addon .ant-input-search-button, .ant-input-search-enter-button input + .ant-input-group-addon .ant-input-search-button,
.ant-btn:not(.ant-btn-circle):not(.ant-btn-circle-outline).ant-btn-icon-only {
  margin: 0;
}

/* Customized search bar with Select component and hidden Input */

.search-select .ant-input-group-addon:first-child {
  width: 100%;
  padding: 0;
}

.search-select .ant-input-group-addon .ant-select {
  width: 100% !important;
}

.search-select .ant-input-group-addon .ant-select-focused .ant-select-selection {
  color: rgba(0, 0, 0, 0.65);
}

.search-select .ant-input-group-addon:last-child {
  padding: 0;
  width: auto;
  display: inline-block;
}

.search-select .ant-btn-icon-only {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.search-select .ant-input-group .ant-input {
  display: none;
}
html {
  position: relative;
  min-height: 100vh;
  font-size: 62.5%;
  color: white;
}

html body {
  background-color: #efefef;
}

.header, .footer {
  -webkit-align-items: center;
          align-items: center;
  background-color: #282c34;
  color: #ffffff;
  display: -webkit-flex;
  display: flex;
  font-size: 2rem;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  min-height: 3rem;
  padding: 1rem;
  width: 100%;
}

.nav-content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 3rem;
  pointer-events: none;
}

.link-green {
  color: #61dafb;
}

.link:hover {
  color: #ffffff;
}

aside {
  border: 1px solid #ccc;
  min-width: 20vw;
}

aside .ant-affix {
  min-height: 90vh;
}

aside .ant-anchor-wrapper {
  background: transparent;
}

main {
  min-height: 90vh;
  padding: 0 1rem;
}

main h2, main h3 {
  border-bottom: 1px solid #aaaaaa;
  margin: 2rem 0;
  padding-bottom: 1rem;
}

.header h1 {
  color: #ffffff;
  font-size: 2rem;
}

main h2 {
  font-size: 2.5rem;
}

main h3 {
  font-size: 2rem;
}

main h4 {
  font-size: 1.5rem;
}

main p {
  font-size: 1.2rem;
}

.fgrow {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

